<template>
	<div>
		<Loader type="sentences" v-if="isLoading" />
		<v-card v-else class="mb-6">
			<v-card-title class="pb-0">
				{{ dataTraction.judul }}
			</v-card-title>
			<v-card-text>
				{{ dataTraction.description }}
			</v-card-text>
		</v-card>
		<v-form v-model="valid" ref="form">
			<template v-if="dataResponse.detail.length > 0">
				<v-card					
					class="mb-6" 
					v-for="(item, i) in dataResponse.detail" :key="i">
					<v-card-title class="pt-3">
						{{ item.pertanyaan }}
					</v-card-title>
					<v-card-text>
						<v-text-field
							label="Jawaban"
							v-model="item.jawaban"
							:rules="requiredRule"
							dense
						></v-text-field>
					</v-card-text>
				</v-card>
			</template>
			<template v-else>
				<v-card
					class="mb-6" 
					v-for="(item, i) in dataTraction.pertanyaan" :key="i">
					<v-card-title class="pt-3">
						{{ item.pertanyaan }}
					</v-card-title>
					<v-card-text>
						<v-text-field
							label="Jawaban"
							v-model="jawaban[i]"
							:rules="requiredRule"
							dense
						></v-text-field>
					</v-card-text>
				</v-card>
			</template>

      <Notif :msg="msg" :status="status" @visible="visible" />
			<div class="d-flex">
        <v-spacer></v-spacer>
        <v-btn
          color="primary"
          text
          @click="$router.go(-1)">
          kembali
        </v-btn>
        <v-btn
          :disabled="!valid"
          :loading="isLoading"
          color="primary"
          @click.prevent="validate"
          type="submit"
          depressed>
          simpan
        </v-btn>
      </div>
		</v-form>  
	</div>
</template>

<script>
	import { mapState } from 'vuex'
  import Notif from '@/components/Notif'
  import Loader from '@/components/Loader'

	export default{
		props: ['traction_id'],

		data: () => ({
			valid: true,
      requiredRule: [v => !!v || 'Data harus diisi'],
      msg: {
        success: 'Berhasil disimpan',
        error: 'Gagal disimpan',
        visible: false
      },
      jawaban: []
		}),

		created() {
			this.$store.dispatch('dataStartupTraction/getDetailTraction', this.traction_id)
			this.$store.dispatch('dataStartupTraction/getDataResponse', this.traction_id)
			this.$store.dispatch('dataStartupTraction/updateIsLoading', true)
		},

    components: {
      Loader,
      Notif
    },

		computed: {
			...mapState('dataStartupTraction', ['dataResponse', 'isLoading', 'status', 'dataTraction']),			
		},

		watch: {
			status(val) {
				if (val === null) this.$router.go(-1)
			}
		},

		methods: {
			validate () {
        if (this.$refs.form.validate()) {
					const data = this.dataTraction.pertanyaan
						.map((soal, i) => {
							if (this.dataResponse.detail.length > 0) {
								return {
									traction_soal_id: soal.id,
									jawaban: this.dataResponse.detail[i].jawaban
								}
							} else {
								return {
									traction_soal_id: soal.id,
									jawaban: this.jawaban[i]
								}
							}
							
						})
          this.$store.dispatch('dataStartupTraction/postDataTraction', data)
          this.$store.dispatch('dataStartupTraction/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataStartupTraction/updateStatus', null)
      }
		}
	}
</script>